.container {
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.search-form {
  text-align: center;
}

.form-control {
  margin: 20px 0;
}

.search-btn {
  margin-left: 8px;
}

.show-more-btn {
  align-items: center;
  gap: 5px;
  margin: 20px auto;
  display: flex;
}

.spinner {
  width: 20px;
  height: 20px;
}

.show-more-btn.is-hidden, .show-more-btn > .spinner.is-hidden {
  display: none;
}

.images {
  width: 1400px;
  gap: 10px;
  align-items: top;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.article__item {
  width: 265px;
  box-shadow: 6px 6px 15px rgba(0, 0, 0, .2);
}

.images__item {
  transition: transform .2s;
  display: block;
}

.images__item:hover {
  cursor: pointer;
  transform: scale(1.5);
}

